import toolState from '../../store/tool-state'
import './toolbar.scss';

function SettingBar() {
  const changeColor = (e) => {
    toolState.setStrokeColor(e.target.value);
    toolState.setFillColor(e.target.value);
  }

  return (
    <div className='toolbar' style={{top: 50}}>
      <div>
        <label> </label>
        <input
          onChange={(e) => toolState.setWidthLine(e.target.value)}
          type='number'
          defaultValue={1}
          min={1}
          max={50}
        />
      </div>
      <div>
        <label htmlFor=""> </label>
        <input onChange={(e) => changeColor(e)} type="color" />
      </div>
      <div>
        <label htmlFor=""> </label>
        <input onChange={(e) => toolState.setStrokeColor(e.target.value)} type="color" />
      </div>
    </div>
  );
}

export default SettingBar;
