import Tool from './Tool'

export default class Circle extends Tool {
  constructor(canvas, socket, session) {
    super(canvas, socket, session);

    this.mouseDownHandler = this.mouseDownHandler.bind(this);
    this.mouseMoveHandler = this.mouseMoveHandler.bind(this);
    this.mouseUpHandler = this.mouseUpHandler.bind(this);
    this.listen();
  }

  listen() {
    this.canvas.addEventListener('mousemove', this.mouseMoveHandler);
    this.canvas.addEventListener('mousedown', this.mouseDownHandler);
    this.canvas.addEventListener('mouseup', this.mouseUpHandler);
  }

  destroyListeners() {
    this.canvas.removeEventListener('mousemove', this.mouseMoveHandler);
    this.canvas.removeEventListener('mousedown', this.mouseDownHandler);
    this.canvas.removeEventListener('mouseup', this.mouseUpHandler);
  }

  mouseUpHandler(e) {
    this.mouseDown = false;
    this.socket.emit('draw', JSON.stringify({
      type: 'circle',
      id: this.session,
      strokeColor: this.strokeColor,
      fillColor: this.fillColor,
      widthLine: this.lineWidth,
      x: this.currentX,
      y: this.currentY,
      rx: this.width / 2,
      ry: this.height / 2
    }));
  }

  mouseDownHandler(e) {
    this.mouseDown = true;
    this.startX = e.pageX - e.target.offsetLeft;
    this.startY = e.pageY - e.target.offsetTop;
    this.saved = this.canvas.toDataURL();
  }

  mouseMoveHandler(e) {
    if (this.mouseDown) {
      this.currentX = e.pageX - e.target.offsetLeft;
      this.currentY = e.pageY - e.target.offsetTop;
      this.width = Math.abs(this.currentX - this.startX);
      this.height = Math.abs(this.currentY - this.startY);
      this.draw(this.currentX, this.currentY, this.width / 2, this.height / 2);
    }
  }

  draw(x, y, rx, ry) {
    const img = new Image();
    img.src = this.saved;
    img.onload = () => {
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.ctx.drawImage(img, 0, 0, this.canvas.width, this.canvas.height);
      this.ctx.beginPath();
      this.ctx.ellipse(x, y, rx, ry, 0, 2 * Math.PI, false);
      this.ctx.fill();
      this.ctx.stroke();
    }
  }

  static draw(ctx, x, y, rx, ry, strokeColor, fillColor, lineWidth) {
    const prevstrokeStyle = ctx.strokeStyle;
    const prevFillStyle = ctx.fillStyle;
    const prevLineWidth = ctx.lineWidth;

    ctx.strokeStyle = strokeColor;
    ctx.fillStyle = fillColor;
    ctx.lineWidth = lineWidth;

    ctx.beginPath();
    ctx.ellipse(x, y, rx, ry, 0, 2 * Math.PI, false);
    ctx.fill();
    ctx.stroke();

    ctx.strokeStyle = prevstrokeStyle;
    ctx.fillStyle = prevFillStyle;
    ctx.lineWidth = prevLineWidth;
  }
} 