import { makeAutoObservable } from 'mobx'

class SocketState {
  socket = null;
  session = localStorage.getItem('session') || null;

  constructor() {
    makeAutoObservable(this);
  }

  setSocket(socket) {
    this.socket = socket;
  }

  setSession(session) {
    this.session = session;
    localStorage.setItem('session', session);
  }
}

export default new SocketState();
