import { observer } from 'mobx-react-lite'
import { useEffect, useRef } from 'react'
import { io } from 'socket.io-client'
import { SOCKET_SERVER } from '../../config'
import canvasState from '../../store/canvas-state'
import socketState from '../../store/socket-state'
import toolState from '../../store/tool-state'
import Brush from '../../tools/Brush'
import Circle from '../../tools/Circle'
import Rect from '../../tools/Rect'
import './canvas.scss';

const Canvas = observer(() => {
  const canvasRef = useRef();

  useEffect(() => {
    canvasRef.current.width = window.innerWidth;
    canvasRef.current.height = window.innerHeight - 100;
    canvasState.setCanvas(canvasRef.current);

    const socket = socketState.socket || io(SOCKET_SERVER);
    if (socket) {
      socketState.setSocket(socket);
      toolState.setTool(new Brush(canvasRef.current, socket, socketState.session));
      const context = canvasRef.current.getContext('2d');
      socket.on('finish', () => {
        context.beginPath();
      })
      socket.on('draw', msg => {
        const data = JSON.parse(msg);
        switch (data.type) {
          case 'brush': 
            Brush.draw(context, data.x, data.y, data.strokeColor, data.fillColor, data.widthLine)
            break;
          case 'rect':
            Rect.draw(context, data.x, data.y, data.width, data.height, data.strokeColor, data.fillColor, data.widthLine)
            break;
          case 'circle':
            Circle.draw(context, data.x, data.y, data.rx, data.ry, data.strokeColor, data.fillColor, data.widthLine)
            break;
          default:
            console.error('wrong tool');
        }
      })
    }
  }, []);

  const mouseDownHandler = () => {
    canvasState.pushToUndo(canvasRef.current.toDataURL());
  }

  return (
    <div className='canvas'>
      <canvas onMouseDown={e => mouseDownHandler()} ref={canvasRef} width={600} height={400}></canvas>
    </div>
  );
})

export default Canvas;
