import './toolbar.scss';
import { Pencil, SquareFill, CircleFill, Eraser, Arrow90degLeft, Arrow90degRight } from 'react-bootstrap-icons'
import toolState from '../../store/tool-state'
import Brush from '../../tools/Brush'
import canvasState from '../../store/canvas-state'
import Rect from '../../tools/Rect'
import Circle from '../../tools/Circle'
import { default as EraserTool } from '../../tools/Eraser';
import socketState from '../../store/socket-state'

function Toolbar() {
  return (
    <div className='toolbar'>
      <button 
        onClick={() => toolState.setTool(new Brush(canvasState.canvas, socketState.socket, socketState.session))}
        className='btn btn-primary'
      >
        <Pencil></Pencil>
      </button>
      <button 
         onClick={() => toolState.setTool(new Rect(canvasState.canvas, socketState.socket, socketState.session))}
        className='btn btn-primary'
      >
        <SquareFill></SquareFill>
      </button>
      <button 
        onClick={() => toolState.setTool(new Circle(canvasState.canvas, socketState.socket, socketState.session))}
        className='btn btn-primary'
      >
        <CircleFill></CircleFill>
      </button>
      <button 
        onClick={() => toolState.setTool(new EraserTool(canvasState.canvas, socketState.socket, socketState.session))}
        className='btn btn-primary'
      >
        <Eraser></Eraser>
      </button>
      <button 
        onClick={() => canvasState.undo()}
        className='btn btn-primary'
      >
        <Arrow90degLeft></Arrow90degLeft>
      </button>
      <button  onClick={() => canvasState.redo()} className='btn btn-primary'>
        <Arrow90degRight></Arrow90degRight>
      </button>
    </div>
  );
}

export default Toolbar;
