import { observer } from 'mobx-react-lite'
import { useEffect } from 'react'
import { io } from 'socket.io-client'
import Canvas from '../../components/canvas/canvas'
import { showAlert } from '../../components/notify/notify'
import SettingBar from '../../components/toolbar/setting-bar'
import Toolbar from '../../components/toolbar/toolbar'
import { SOCKET_SERVER } from '../../config'
import canvasState from '../../store/canvas-state'
import socketState from '../../store/socket-state'

const DrawPage = observer(() => {

  useEffect(() => {
    const socket = socketState.socket || io(SOCKET_SERVER);
    socketState.setSocket(socket);
    socketState.socket.on('user-connect', (msg) => {
      const userName = JSON.parse(msg).userName;
      if (userName) {
        showAlert(` ${userName} `);
      }
    })
    socketState.socket.emit(
      'join-room',
      JSON.stringify({
        id: socketState.session,
        userName: canvasState.userName,
      })
    );
  }, []);

  return (
    <>
      <SettingBar></SettingBar>
      <Toolbar></Toolbar>
      <Canvas></Canvas>
    </>
  );
})

export default DrawPage;
