import { observer } from 'mobx-react-lite'
import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { io } from 'socket.io-client'
import { SOCKET_SERVER } from '../../config'
import canvasState from '../../store/canvas-state'
import socketState from '../../store/socket-state'
import './sessions.scss';

const SessionsPage = observer(() => {
  const [userName, setUserName] = useState('');
  const [rooms, setRooms] = useState([]);
  const navigate = useNavigate();

  const changeUserName = (userName) => {
    canvasState.setUserName(userName);
    setUserName(userName);
  }

  useEffect(() => {
    const socket = io(SOCKET_SERVER);
    socketState.setSocket(socket);
    socket.on('init-connect', (msg) => {
      const rooms = JSON.parse(msg).rooms;
      setRooms(rooms);
    });
    socket.on('id-room', (msg) => {
      const currentIdRoom = JSON.parse(msg).id;
      socketState.setSession(currentIdRoom);
      navigate(`/${currentIdRoom}`);
    });
    socket.on('new-rooms', (msg) => {
      const rooms= JSON.parse(msg).rooms;
      setRooms(rooms);
    });
  }, [navigate]);

  const createRoom = () => {
    if (!socketState.socket) return;
    socketState.socket.emit('new-room');
  }

  const joinRoom = (roomId) => {
    if (roomId) {
      socketState.setSession(roomId);
    }
    if (!socketState.socket) return;
    socketState.socket.emit(
      'join-room',
      JSON.stringify({
        id: roomId || socketState.session,
        userName,
      })
    );
    navigate(`/${roomId}`);
  }

  return (
    <div className='w-50 mx-auto sessions-list'>
      <input value={userName} onChange={e => changeUserName(e.target.value)} type="text" placeholder=' ' />
      <button onClick={() => createRoom()} className='btn btn-primary'>
          
      </button>
      <h1> </h1>
      <div className='current mt-2 text-center'>
        {rooms.map(room => (
          <button onClick={() => joinRoom(room)} key={room} className='mt-2 btn btn-primary'>
              {room}
          </button>))
        }
      </div>
    </div>
  );
})

export default SessionsPage;
