import './alert.scss';

const alertContainer = document.getElementsByClassName('alerts__container')[0];

export function showAlert(text) {
  const alertElem = document.createElement('div');
  alertElem.textContent = text;
  alertElem.classList.add('alert');
  alertContainer.appendChild(alertElem);

  const transition = [
    {
      opacity: 0,
      transform: 'translate(400px, 0)',
    },
    {
      opacity: 1,
      transform: 'translate(0, 0)',
    },
  ];

  const animate = alertElem.animate(transition, {
    duration: 1000,
    easing: 'ease-out',
  });

  setTimeout(() => {
    animate.reverse();
    animate.onfinish = () => {
      alertElem.remove();
    };
  }, 2000);
}
